/* Main container styling */
.phonebook-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 2rem;
    font-family: Arial, sans-serif;
    color: #333;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

/* Header styling */
.header h1 {
    text-align: center;
    color: #333;
    font-size: 2rem;
    margin-bottom: 1.5rem;
}

/* Section styling */
.form-section,
.numbers-section,
.filter-section {
    margin-bottom: 2rem;
}

/* Form styling */
.form-section h2,
.numbers-section h2,
.filter-section h3 {
    color: #5a5a5a;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

/* Content sections */
.content {
    display: flexbox;
    flex-direction: column;
    gap: 1rem;
}

/* Button styling */
button {
    background-color: #ff6666;
    color: #fff;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #ff4c4c;
}

/* Contacts list */
.numbers-section ul {
    list-style-type: none;
    padding: 0;
}

.numbers-section li {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
    border-bottom: 1px solid #ddd;
}

.name_input {
    margin-left: 26px;
}

.number_input {
    margin-left: 12px;
}

.content {
    margin-top: 60px;
}

.warning {
    position: absolute;
}
